zation with mankind forging ahead with the civilization upgrading. ")]),_c('p',[_vm._v(" Actually, Mayan prophecy of 2012 means the extermination of the third-dimensional earth. That is, the earth will ascend to the fourth even the fifth level and become a purer planet where humans are more intellectual and can echo with the cosmos. ")]),_c('p',[_vm._v(" The earth was in the third dimension. After 2012, the earth’s magnetic field will be lifted and gradually step into the fourth dimension, which is equal to three-dimensional space plus one-dimensional time. ")]),_c('p',[_vm._v(" The third dimension is the space in which we live whereas the fourth marks the addition of the time axis. Under the gravitational force of the earth and other large planets or stars, space-time will contort thereby the former will be knocked out and the latter is required. ")]),_c('p',[_vm._v(" According to the Maya’s Long Count Calender，December 21, 2012 will be the end of this cycle of human civilization, and mankind will enter a brand new civilization. With reference to Maya predictions and in-depth research on Bitcoin and blockchain, “Rainbow City” believes that the \"digital Bit civilization\" established by Satoshi Nakamoto is the new human civilization predicted by the Maya. ")]),_c('p',[_vm._v(" In comparison to the Mayan prophecy and the incisive understanding of Bitcoin and blockchain, Rainbow City conceives the notion that the digital Bit civilization established by Satoshi is the new human civilization predicted by Maya. ")]),_c('p',[_vm._v(" Now digital currency has been applied worldwide and the blockchain tech attained the recognition of various countries, formatting and purifying the entire globe. Explicitly, a brand-new human civilization has arrived. ")]),_c('p',[_vm._v(" Human civilization will also evolve from a carbon-based civilization to a silicon-based one, permeating into human production and distribution. The essence of the blockchain is a database, which mechanized and digitized mankind. But it's not merely for humans, machines and AI have access to it as well. ")]),_c('p',[_vm._v(" The future blockchain built is of sci-fi digital social ecosystem, linking the atomic physical world of carbon-based civilization and the digital Bitcoin space of silicon-based civilization. Blockchain created a language shared by human beings and artificial intelligence thereafter human and human, human and machine, machine and machine can all cooperate with each other in the Bitcoin world. ")]),_c('p',[_vm._v(" Only when silicon-based life forms emerge and participate in social division of labor, can human civilization exceed earth civilization, truly setting foot on cosmic civilization. Crucially, the open data of blockchain is the soil where silicon-based life forms sprout and thrive. ")]),_c('p',[_vm._v(" In 2009, Satoshi Nakamoto created Bitcoin. In 2012, the moving earth and solar system passed a beam of galactic rays from the core of the galaxy. After the renewal period of the earth, two major events will occur for mankind: the purification and regeneration of the earth, and the emergence of new human beings that can resonate with the universe. ")]),_c('p',[_vm._v(" If blockchain's formatting the entire earth civilization is the purification and regeneration of the earth, then where are the new humans who can resonate with the universe? ")]),_c('p',[_vm._v(" Rainbow City harbors the idea that those new humans are rainbow warriors who have been preaching for Satoshi Nakamoto, for Bitcoin and for blockchain！ ")]),_c('p',[_vm._v(" Now let us take a closer look at another Mayan prophecy in regard to the rainbow warriors. ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"table-title  animate__animated animate__flipInX"},[_vm._v(" Ancient Mayan prophecy "),_c('br'),_vm._v(" the emergence of Rainbow warrior ")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c