nhancement, a group of Satoshi Nakamoto's believers made up their mind to re-establish the rainbow contract with God and recreate the Tower of Babel, namely, Rainbow City. This time we plan to have Satoshi Nakamoto's name up, the Creator of the Bitcoin world, not human beings'. ")]),_c('p',[_vm._v(" We want to make another contract with God in the name of \"Rainbow\", and the \"Rainbow City\" we created is a Bitcoin civilized digital city-state based on \"Token Economy\" and \"Community Governance\". In the system of \"Rainbow City\", we will issue \"Rainbow Token-RBT\", and community members who have \"Rainbow Token-RBT\" will all be called \"Rainbow Warriors\". The \"Rainbow Warriors\" defend the consensus that man and nature live in harmony. This is the Satoshi Nakamoto Bitcoin consensus! ")]),_c('p',[_vm._v(" Under the leadership of \"Rainbow\", different beliefs and races will unite to spread the concept that human beings should live in harmony with nature. Anyone with this consciousness can become a \"Rainbow Warrior\" who are able to resonate with the consciousness of the universe,and promote the earth’s civilization into the dimension of time. ")])])}]

